@import 'styles';

@media only screen and (max-width: 1083px){
  .text-intro {
    p {
      text-align: center;
      max-width: 400px;
    }
  }
  .title {
    font-size: 3em;
  }
  .title-how-we-work {
    font-size: 2.4em !important;
  }
}

@media only screen and (max-width: 894px){
  .title {
    font-size: 2.7em;
  }
  .title-how-we-work {
    font-size: 2em !important;
  }
}

//// mobile

@media only screen and (max-width: 813px){

/// header mobile

  .headerBtn-Menu {
    display: block;
    background-repeat: no-repeat;
    padding: 5px 20px;
    text-indent: -9999px;
    background-position: -20px right;
    // z-index: 300;
  }
  
  .headerBtnMenu-open {
    background-image: url(/images/icon-hamburger.svg);
    width: 100%;
  }
  
  .headerBtnMenu-close {
    background-image: url(/images/icon-close.svg);
  }

  .headerMenu {
    padding: 40px;
    grid-template-columns: 1fr;
    background: var(--very-light-gray) url(/images/bg-pattern-mobile-nav.svg) no-repeat;
    background-size: cover;
    z-index: 100;

    position: absolute;
    width: 100%;
    top: 0;
    right: 0;    

    transform: translateY(-150%);
    transition: transform 200ms linear;
  }

  .menuHeader-open {
    transform: translateY(0);
  }

  //// intro
  .intro-bg {
    padding: 0px;
    &::after {
      content: none;
    }
    &::before {
      content: none;
    }
  }
  .intro {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .image-intro {
    justify-self: center;
    position: static;
    margin-bottom: 0;
    width: 100%;
    height: 750px;
  }
  .text-intro {
    text-align: center;
    order: 2;
    justify-items: center;
    padding: 90px 20px 100px 20px;
    position: relative;
    &::before {
      content: '';
      width: 200px;
      height: 400px;
      background: url(/images/bg-pattern-intro-right-mobile.svg) no-repeat;
      background-position: right bottom;
      background-size: 200px 400px;
      position: absolute;
      z-index: 2;
      right: 0;
      bottom: -210px;
    }
    &::after {
      content: '';
      width: 200px;
      height: 170px;
      background: url(/images/bg-pattern-intro-left-mobile.svg) no-repeat;
      background-position: top lef;
      background-size: 200px 170px;
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
    }
  }
  .title-intro {
    padding-top: 0px;
    &::before{
      content: none;
    }
  }

  //// were diferrent

  .title-were-different {
    text-align: center;
    &::before{
      content: '';
      margin: auto;
    }
  }
  .differences {
    grid-template-columns: 1fr;
    > div {
      display: grid;
      justify-items: center;
      text-align: center;
      p {
        max-width: 450px;
      }
    }
    }

    //// how-we-work
    .how-we-work {
      grid-template-columns: 1fr;
      justify-items: center;
      gap: 40px;
      text-align: center;
      background: var(--dark-violet) url(/images/bg-pattern-how-we-work-mobile.svg) no-repeat;
      background-position: right;
      background-size: contain;
    }


    ////footer 
    .footer-bg {
      background: var(--very-light-gray) url(/images/bg-pattern-footer-mobile.svg) no-repeat;
      background-position: top;
      background-size:  contain;
    }
    .logo-social {
      grid-template-columns: 1fr;
      justify-items: center;
      gap: 30px;
    }
    .nav-footer {
      grid-template-columns: 1fr;
      justify-items: center;
      text-align: center;
    }

  }

