:root {
  --dark-violet: hsl(256, 26%, 20%);
  --grayish-blue: hsl(216, 30%, 68%);
  --very-dark-violet: hsl(270, 9%, 17%);
  --dark-grayish-violet: hsl(273, 4%, 51%);
  --very-light-gray: hsl(0, 0%, 98%);
  --font-body: 'Karla', sans-serif;
  --font-Headings: 'DM Serif Display', serif;
}

body {
  font-family: var(--font-body);
}

.link {
  padding: 8px 30px;
  text-transform: uppercase;
  border: 2px solid currentColor;
  z-index: 100;
}

.title {
  font-family: var(--font-Headings);
  font-size: 3.7em;
  line-height: 1;
  letter-spacing: 0.1em;
  &::before{
    content: '';
    display: table;
    width: 160px;
    height: 2px;
    position: relative;
    top: -68px;
  }
}

//// Header

.headerBtn-Menu {
  display: none;
}

.header {
  max-width: 1120px;
  margin: 0 auto;
  padding: 30px 20px;
  display: grid;
  justify-content: space-between;
  align-items: center;
  grid-template-columns: max-content max-content;
}
.headerMenu {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  gap: 20px;
  li {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    a {
      text-decoration: none;
      padding: 10px 0;
    }
    a:active, a:link, a:visited  {
        color: var(--dark-grayish-violet);
    }
    a:hover{
      color: var(--dark-violet)
    }
  }
}

.link-header, .link-header:link, .link-header:active, .link-header:visited {
  color: var(--dark-violet) !important;
  // border: 2px solid var(--dark-violet);
  padding: 8px 30px !important;
}
.link-header:hover {
  color: var(--very-light-gray) !important;
  background: var(--dark-violet);
}

//// intro

.intro-bg {
  padding: 0 20px;
  margin-bottom: 370px;
  background: var(--dark-violet);
  position: relative;
  &::before {
    content: '';
    height: 500px;
    width: 350px;
    background: url(/images/bg-pattern-intro-right-desktop.svg) no-repeat;
    background-position: top right;
    background-size: 350px 500px;
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
  }
  &::after {
    content: '';
    width: 200px;
    height: 500px;
    background: url(/images/bg-pattern-intro-left-desktop.svg) no-repeat;
    background-position: bottom left;
    background-size: 200px 500px;
    position: absolute;
    z-index: 2;
    left: 0px;
    bottom: -350px;
  }
}

.intro {
  max-width: 1120px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  color: var(--very-light-gray);
  p {
    font-size: 1em;
  }
}

.text-intro {
  display: grid;
  grid-template-rows: repeat(3, max-content);
  gap: 30px;
  padding-bottom: 116px;
}

.title-intro {  
  padding-top: 160px;
  font-family: var(--font-Headings);
  &::before{  
    background: var(--very-light-gray);
  }
}

.link-view-plans, .link-view-plans:link, .link-view-plans:active, .link-view-plans:visited {
  color: var(--very-light-gray);
  max-width: 160px;
  text-decoration: none;
  &:hover {
    color: var(--dark-violet);
    background: var(--very-light-gray);
  }
}

.image-intro {
  position: relative;
  bottom: -104px;
  margin-bottom: -80px;
}


//// were-different

.were-different {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 20px 150px 20px;
}

.title-were-different {
  margin-bottom: 90px;
  &::before{
    background: var(--dark-grayish-violet);
  }
}
.differences {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  img {
    margin-bottom: 30px;
  }
  h3 {
    margin-bottom: 16px;
    font-size: 1.5em;
  }
  p {
    color: var(--dark-grayish-violet);
  }
}


//// how-we-work
.how-we-work-section {
  padding: 0 20px;
}

.how-we-work {
  max-width: 1120px;
  margin: 0 auto 150px auto;
  padding: 70px 80px;
  display: grid;
  grid-template-columns: repeat(2, max-content);
  justify-content: space-between;
  align-items: center;
  background: var(--dark-violet) url(/images/bg-pattern-how-we-work-desktop.svg) no-repeat;
  background-position: right;
  background-size: contain;


  h1 {
    font-family: var(--font-Headings);
    font-size: 2.6em;
    color: var(--very-light-gray);
  }
}
.link-how-we-work {
  color: var(--very-light-gray);
  text-decoration: none !important;
  &:hover {
    color: var(--dark-violet);
    background: var(--very-light-gray);
  }
}

//// footer

.footer-bg {
  padding: 60px 20px 0 20px;
  background: var(--very-light-gray) url(/images/bg-pattern-footer-desktop.svg) no-repeat;
  background-position: top left;
  background-size: 530px 220px;
}

.logo-social {
  margin: 0 auto;
  max-width: 1120px;
  display: grid;
  grid-template-columns: repeat(2, max-content);
  justify-content: space-between;
  padding-bottom: 30px;
  border-bottom: 1px solid var(--dark-grayish-violet);
}

.social-media {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  gap: 16px;
  a svg {
    transition: 0.1s;
    fill: var(--dark-grayish-violet);
    &:hover {
      transition: 0.1s;
      fill: var(--dark-violet);
    }
    }
}


.nav-footer {
  max-width: 1120px;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 60px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);

  ul li{
    text-transform: uppercase;
    margin: 0 0 20px 0;
    font-weight: 700;
    letter-spacing: 0.1em;
    a {
      padding: 10px 0;
      &:active, &:link, &:visited {
        color: var(--dark-violet);
      }
    }
  }
}

.title-nav-footer {
  color: var(--dark-grayish-violet) !important;
  text-transform: uppercase;
  margin-bottom: 40px !important;
}

.attribution {
  margin: 0 auto;
  max-width: 1120px;
  text-align: center;
}